import React, { useRef } from 'react'

const AddImage = props => {

    const imgRef = useRef(null)

    const img_pathUrl = (input) => {
        imgRef.target.src = (window.URL ? URL : webkitURL).createObjectURL(input.files[0]);
    }

    let content = (
        <div>
            <div>
                <img className="h-16 w-16" src="" ref={imgRef} alt="your image"/>
                <input
                    accept="image/*,image/heif,image/heic,video/*,video/mp4,video/x-m4v,video/x-matroska"
                    multiple
                    type="file"
                    onChange={(input) => img_pathUrl(input)}
                />
                <div role="button" tabIndex="0">
                    <div>
                        <span>
                            <i role="img"></i>
                        </span>
                        <span dir="auto">
                            Photo/Video
                        </span>
                    </div>
                    <div data-visualcompletion="ignore">
                    </div>
                </div>
            </div>
        </div>
    )

    return content
}

export default AddImage